"use client"
import { useEffect, useRef } from 'react'
import Script from 'next/script'

declare global {
  interface Window {
    am5: any;
    am5map: any;
    am5geodata_worldLow: any;
    am5geodata_usaLow: any;
    am5themes_Animated: any;
  }
}

export default function MapViz() {
  const chartRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (!chartRef.current || !window.am5) return

    // Create root element
    const root = window.am5.Root.new(chartRef.current)

    // Set themes
    root.setThemes([window.am5themes_Animated.new(root)])

    // Create the map chart
    const chart = root.container.children.push(
      window.am5map.MapChart.new(root, {
        panX: "translateX",
        panY: "translateY",
        projection: window.am5map.geoMercator(),
        backgroundSeries: {
          mapPolygons: {
            fill: window.am5.color("#0A5C35"),
            fillOpacity: 0.1
          }
        }
      })
    )

    // Create main polygon series for countries
    const polygonSeries = chart.series.push(
      window.am5map.MapPolygonSeries.new(root, {
        geoJSON: window.am5geodata_worldLow,
        exclude: ["AQ"]
      })
    )

    // Set up the color scale for energy production
    const colorScale = window.am5.ColorSet.new(root, {
      colors: [
        window.am5.color("#0A5C35"),  // Dark green
        window.am5.color("#0D7A4A"),  // Medium green
        window.am5.color("#4ADE80"),  // Light green
        window.am5.color("#22C55E"),  // Bright green
        window.am5.color("#86EFAC")   // Very light green
      ]
    })

    polygonSeries.mapPolygons.template.setAll({
      tooltipText: "{name}",
      toggleKey: "active",
      interactive: true,
      fill: colorScale.getIndex(0),
      strokeWidth: 0.5,
      stroke: window.am5.color("#ffffff")
    })

    polygonSeries.mapPolygons.template.states.create("hover", {
      fill: window.am5.color("#4ADE80"),
      fillOpacity: 0.8
    })

    polygonSeries.mapPolygons.template.states.create("active", {
      fill: window.am5.color("#22C55E"),
      fillOpacity: 0.9
    })

    // Add energy-related data visualization
    const pointSeries = chart.series.push(
      window.am5map.MapPointSeries.new(root, {})
    )

    // Add some sample energy points (you can replace these with real data)
    const energyPoints = [
      { geometry: { type: "Point", coordinates: [0, 0] }, name: "Global Energy Hub" },
      { geometry: { type: "Point", coordinates: [30, 30] }, name: "Middle East Energy Center" },
      { geometry: { type: "Point", coordinates: [-100, 40] }, name: "North American Power Grid" },
      { geometry: { type: "Point", coordinates: [100, 40] }, name: "Asian Energy Network" }
    ]

    pointSeries.data.setAll(energyPoints)

    pointSeries.bullets.push(function() {
      return window.am5.Bullet.new(root, {
        sprite: window.am5.Circle.new(root, {
          radius: 5,
          fill: window.am5.color("#4ADE80"),
          tooltipText: "{name}"
        })
      })
    })

    // Add zoom control
    const zoomControl = chart.set("zoomControl", window.am5map.ZoomControl.new(root, {}))
    zoomControl.homeButton.set("visible", true)

    // Set clicking on "water" to zoom out
    const background = chart.chartContainer.get("background")
    if (background) {
      background.events.on("click", function () {
        chart.goHome()
      })
    }

    // Make stuff animate on load
    chart.appear(1000, 100)

    // Cleanup
    return () => {
      root.dispose()
    }
  }, [])

  return (
    <>
      <Script src="https://cdn.amcharts.com/lib/5/index.js" strategy="beforeInteractive" />
      <Script src="https://cdn.amcharts.com/lib/5/map.js" strategy="beforeInteractive" />
      <Script src="https://cdn.amcharts.com/lib/5/geodata/worldLow.js" strategy="beforeInteractive" />
      <Script src="https://cdn.amcharts.com/lib/5/geodata/usaLow.js" strategy="beforeInteractive" />
      <Script src="https://cdn.amcharts.com/lib/5/themes/Animated.js" strategy="beforeInteractive" />
      <div className="flex items-center justify-center w-full h-full bg-gradient-to-b from-[#0A5C35] to-[#022C1A]">
        <div ref={chartRef} style={{ width: '100%', height: '100vh' }} />
      </div>
    </>
  )
} 